.range{position:relative;display:flex;flex-direction:column;justify-content:center;align-items:flex-start;width:calc((100% - 60px)/4);line-height:15px;min-height:45px;border:1px solid rgba(0,0,0,.3);background:#fff;color:#000;border-radius:8px;box-sizing:border-box;cursor:pointer}.range_open{border:1px solid #000;border-bottom:1px solid rgba(0,0,0,0);border-radius:8px 8px 0 0}.range_dark{background:#0c0c0c;border-color:#fff}@media screen and (max-width: 1365px){.range{min-width:calc((100% - 60px)/4)}}@media screen and (max-width: 767px){.range{min-width:100%}}.range_activeDark{border-bottom:1px solid rgba(0,0,0,0)}.range__title{margin:0 0 0 15px;font-style:normal;font-size:13px;line-height:15px;overflow:hidden;text-overflow:ellipsis;white-space:nowrap}.range__title_dark{color:#fff}.range__arrow{position:absolute;display:flex;right:18px;float:right;width:10px;height:6px;fill:rgba(0,0,0,.3);fill-opacity:1}.range__arrow_dark{fill:rgba(255,255,255,.3)}.range__сontainer{position:absolute;display:flex;justify-content:center;align-items:center;align-self:center;background:#fff;min-width:100.9%;min-height:85px;border:1px solid #000;border-style:none solid solid;border-radius:0px 0px 8px 8px;top:43px;left:-1px;box-sizing:border-box;z-index:1}.range__сontainer_active{border-top:none;display:flex}@media screen and (min-width: 3000px){.range__сontainer_active{width:102%;left:-3px;top:41px}}@media screen and (max-width: 1365px){.range__сontainer_active{flex-direction:column;height:160px}}@media screen and (max-width: 820px){.range__сontainer_active{min-width:101.4%}}@media screen and (max-width: 767px){.range__сontainer_active{height:85px;min-width:100.3%}}@media screen and (max-width: 545px){.range__сontainer_active{min-width:100.4%}}@media screen and (max-width: 420px){.range__сontainer_active{min-width:100.6%}}.range__сontainer_dark{background:#0c0c0c;border-color:#fff}